@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,300;0,400;0,600;0,700;1,300;1,400;1,700&display=swap);
.spinner{display:flex;position:fixed;align-items:center;justify-content:center;top:0;left:0;right:0;bottom:0;z-index:801}

.slideshow{overflow:hidden;max-width:500px}.slideshowSlider{white-space:nowrap;transition:ease 1000ms}.slide{display:inline-block;width:100%;border-radius:40px}.slideshowDots{text-align:center}.slideshowDot{display:inline-block;height:20px;width:20px;border-radius:50%;cursor:pointer;margin:15px 7px 0px;background-color:#c4c4c4}.slideshowDot.active{background-color:#0095FF}@media (min-width: 770px){.slideshow{width:40%;margin-right:24px}}@media (max-width: 769px){.slideshow{width:316px}}



