a {
    text-decoration: none;
}
  
input:focus-visible {
    outline: none;
}

select:focus-visible {
    outline: none;
}

/* width */
::-webkit-scrollbar {
    height: 4px;         
    width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.noti__list{position:absolute;top:32px;right:-100px;margin:0px;min-width:320px;background-color:white;box-shadow:0px 4px 8px 0px #ccc;border-radius:8px;padding:0px 8px;padding-bottom:8px;z-index:101}.noti__list ul{padding:0px;max-height:400px;overflow:auto;margin:0}.noti__list li{padding:8px;font-size:16px;list-style-type:none}.noti__list li:hover{cursor:pointer;background-color:#ccc}.noti__list h5{text-align:center;margin:8px 0px;font-size:14px}.noti__list__item{display:flex;align-items:center;justify-content:center;color:black;border-radius:8px}.noti__list__item:hover{background-color:#F0F2F5}.noti__list__item img{width:48px;height:48px;margin-right:0px}.noti__list__item p{font-size:14px;margin:8px 8px}.noti__list__item p span{color:#767676}.noti__list__spinner{display:flex;align-items:center;justify-content:center}@media (max-width: 769px){.noti__list{min-width:280px}}

